import { createContext, useState, useEffect } from "react";
import {
  signInWithEmailAndPassword,
  createUserWithEmailAndPassword,
  onAuthStateChanged,
} from "firebase/auth";
import { auth } from "../firebase-config";
import { GoogleAuthProvider, signInWithPopup, sendPasswordResetEmail } from "firebase/auth";
import { useNavigate } from "react-router-dom";
export const UserContext = createContext();

export function UserContextProvider(props) {
  //Partie utilisateur (cration, identitfication de l'utilisateur)
  const [currentUser, setCurrentUser] = useState("");
  const [idCurrentUser, setIdCurrentUser] = useState("");
  const [userToken, setUserToken] = useState("");
  const [isStudent, setIsStudent] = useState(false);
  const [isEnterprise, setIsEnterprise] = useState(false);
  const [loadingData, setLoadindData] = useState(true);
  const [userInformations, setUserInformations] = useState([])

  //Permet de rcuprer l'identifiant de l'utilisateur courant dans le context
  const getIdUser = (id) => {
    setIdCurrentUser(id);
  };

  //Permet de rcuprer le token de connexion  l'api de l'utilisateur courant dans le context
  const getTokenUser = (token) => {
    setUserToken(token);
  };


  const getUserInformation = (info) => {
    setUserInformations(info);
  };

  //On instantie useNavigate
  const navigate = useNavigate();

  //authentification google
  const provider = new GoogleAuthProvider();

  //Mthode d'enregistrement d'un nouvel utilisateur
  const signUp = (email, pwd) =>
    createUserWithEmailAndPassword(auth, email, pwd);
  //Mthode de connexion d'un utilisateur par mail et mots de passe
  const signIn = (email, pwd) => signInWithEmailAndPassword(auth, email, pwd);

  //Mthode pour envoyer un mail de rinitialisation de mot de passe
  const sendForgotPassword = (email) => {

    console.log("email de rinitialisation envoy  l'adresse mail " + email);
    sendPasswordResetEmail(auth, email)
        .then(() => {
            alert("email de rinitialisation envoy  l'adresse mail " + email);
        })
        .catch(function (e) {
            console.log(e);
        });
};

  //Mthode de connexion d'un utilisateur avec son compte google
  const signInWithGoogle = () =>
    signInWithPopup(auth, provider)
      .then((result) => {
        //On ferma la fentre modal et on redirige vers la page d'accueil prive
        toggleModals("reset");
        navigate("/private/private-home");
      })
      .catch((error) => {
        // Traitement des erreurs
        const errorCode = error.code;
        const errorMessage = error.message;
        // L'email du compte de l'utilisateur utilis.
        const email = error.email;
        // Le type AuthCredential qui a t utilis.
        const credential = GoogleAuthProvider.credentialFromError(error);
      });



  //Partie mthodes pour grer les modales
  const [modalState, setModalState] = useState({
    //Variable d'tat des modales d'inscription et de connection
    signUpModal: false,
    signInModal: false,
  });

  const toggleModals = (modal) => {
    if (modal === "signIn") {
      //Si modal est strictement gale  "signIn", on passe signInModal  true pour afficher la modale de connection
      setModalState({
        signUpModal: false,
        signInModal: true,
        resetModal: false,
      });
    }

    if (modal === "signUp") {
      //Si modal est strictement gale  "signUp", on passe signUpModal  true pour afficher la modale d'inscription
      setModalState({
        signUpModal: true,
        signInModal: false,
        resetModal: false,
      });
    }

    if (modal === "reset") {
      //Si modal est strictement gale  "reset", on passe resetModal  true pour afficher la modale de rinitialisation du MDP
      setModalState({
        signUpModal: false,
        signInModal: false,
        resetModal: true,
      });
    }

    if (modal === "close") {
      //Si modal est strictement gale  "close", on ferme la modale qui est affiche
      setModalState({
        signUpModal: false,
        signInModal: false,
        resetModal: false,
      });
    }
  };

  useEffect(() => {
    //On vrifie si l'utilisateur courant est bien connect avant d'afficher la page d'accueil prive
    const unsubsribe = onAuthStateChanged(auth, (currentUser) => {
      setCurrentUser(currentUser);
      setIsStudent(true);
      navigate("/private/private-home");
      setLoadindData(false);
    });

    return unsubsribe;
  }, []);

  return (
    <UserContext.Provider
      value={{
        modalState,
        toggleModals,
        signUp,
        signIn,
        currentUser,
        getIdUser,
        getTokenUser,
        userToken,
        idCurrentUser,
        signInWithGoogle,
        isStudent,
        isEnterprise,
        getUserInformation,
        sendForgotPassword
      }}
    >
      {!loadingData && props.children}
    </UserContext.Provider>
  );
}
