import React, { useContext } from "react";
import { UserContext } from "../../context/userContext";
import { Link } from "react-router-dom";
import { signOut } from "firebase/auth";
import { useNavigate } from "react-router-dom";
import { auth } from "../../firebase-config";
import logo from "./../../images/Logo_Altizy.png";
import Button from "../buttons/Button";
import "./navbar.css";

function Navbar() {
  const { toggleModals, currentUser, isStudent } = useContext(UserContext);

  const navigate = useNavigate();

  const logOut = async () => {
    try {
      await signOut(auth);
      navigate("/");
    } catch {
      alert(
        "Pour des raisons qui nous sont obscure, vous ne pouvez pas vous dconnecter... Veuillez vrifier votre connexion internet et ressayez !"
      );
    }
  };

  return (
    <nav className="navbar navbarlight bg-light px-4">
      <div className="menu">
        <Link to="/private/private-home" className="navbar-brand">
          <img style={{ height: "86px" }} src={logo} alt="logo" />
        </Link>

        <div className="menu1">
          {!currentUser && <Button label="Prsentation " href="#presentation" />}

          {!currentUser && <Button label="quipe" href="#equipe" />}

          {!currentUser && <Button label="Avis" href="#avis" />}

          {!currentUser && <Button label="Contact" href="#contact" />}
        </div>
      </div>

      <div className="menu2">
        {!currentUser && (
          <button onClick={() => toggleModals("signUp")} className="btn ">
            S'inscrire
          </button>
        )}
        {!currentUser && (
          <button onClick={() => toggleModals("signIn")} className="btn ms-2">
            Se connecter
          </button>
        )}
        {currentUser && (
          <>
            {isStudent ? (
              <Button
                label="Ma candidature"
                url="/private/private-myapplication"
              />
            ) : (
              <Button label="Mes annonces" url="/private/private-myaccount" />
            )}
            <Button label="Mon compte" url="/private/private-myaccount" />
            <button onClick={logOut} className="btn ms-2 text-danger">
              Se dconnecter
            </button>
          </>
        )}
      </div>
    </nav>
  );
}

export default Navbar;
