import React from "react";
import Carouselavis from "../components/carousel/CarouselAvis";
import Formcontact from "../components/Form/FormContact";
import "./Home.css";

function Home() {
  return (
    <div className="public-home">
      <div className="welcome-banner"></div>
      <div className="section-row" id="presentation">
        <div className="content-image1"></div>
        <div className="content-text1">
          <p>
            <p>Quest-ce quAltizY ?</p> <p>AltizY est une plateforme qui met en
            relation les tudiants  la recherche dune alternance ou dun stage
            avec des entreprises qui recrutent. Notre mission aujourdhui, est
            daider les tudiants  trouver lalternance ou le stage qui leur
            correspond. Vous tes une entreprise ? Nous sommes la solution pour
            vos recrutements. </p>
          </p>
        </div>
      </div>

      <div className="section-column" id="equipe">
        <div className="title-image">
          <div className="title">
            <h1>
              Notre <br></br>quipe
            </h1>
          </div>
          <div className="content-image2"></div>
        </div>
        <div className="content-text2">
          <p>
            Nous sommes une quipe de 5 tudiants, nous sommes tous passs par
            lalternance, nous savons que ce nest pas toujours facile. Cest
            pour cette raison que nous avons dcid de crer AltizY. Alors
            aujourdhui, retrouvez, Manon, Chlo, Johanna, Agnor et Alexandre
            pour trouver lalternance qui vous convient!
          </p>
        </div>
      </div>

      <Carouselavis />
      <Formcontact />
    </div>
  );
}

export default Home;
