import React, { useState, useCallback, useContext } from "react";
import Radiotogglebuttons from "./../buttons/RadioToggleButtons";
import { useNavigate } from "react-router-dom";
import { UserContext } from "./../../context/userContext";

import * as api from "./../../api-service";

function Formenterpriseaccount(props) {
  //On instantie useNavigate
  const navigate = useNavigate();

  //On instantie useContext
  const { idCurrentUser } = useContext(UserContext);

  //id de l'utilisateur courant
  const id = idCurrentUser;

  //Composant bouton radio
  const radioButtons = props.radioButtons;

  //On cr un objet "initialUserData" json qui servira pour le POST des donnes concernant l'entreprise via l'API REST
  const initialUserData = {
    utilisateur: `/api/utilisateurs/${id}`,
    siren: "",
    nom: "",
    email: "",
    photo: "",
    interlocuteur: "",
    description: "",
  };

  //Mthodes permettant de mettre  jour les informations de l'utilisateur dans l'objet JSON
  const [userData, setUserData] = useState(initialUserData);
  const updateUserDataHandler = useCallback(
    (type) => (event) => {
      setUserData({ ...userData, [type]: event.target.value });
    },
    [userData]
  );

  //Mthode POST des donnes vers la BDD
  const handleForm = (e) => {
    e.preventDefault();
    console.log(userData);

    api
      .postUser("http://127.0.0.1:8000/api/entreprises", userData)
      .then((res) => res.json())
      .then((result) => {
        console.log(result);
      });

    navigate("/private/private-home");
  };

  return (
    <>
          <div className="form-group required">
            <label htmlFor="InputSiren" className="control-label">
              Numro de SIREN
            </label>
            <input
              required
              value={userData.siren}
              onChange={updateUserDataHandler("siren")}
              type="number"
              className="form-control"
              id="InputSiren"
              aria-describedby="siren"
              placeholder="Entrez votre numro de SIREN"
            />
          </div>
          <div className="form-group required">
            <label htmlFor="InputNomSociete" className="control-label">
              Nom de la socit
            </label>
            <input
              required
              value={userData.nom}
              onChange={updateUserDataHandler("nom")}
              type="text"
              className="form-control"
              id="InputNomSociete"
              aria-describedby="Nom Societe"
              placeholder="Entrez le nom de votre socit"
            />
          </div>
          <div className="form-group required">
            <label htmlFor="InputEmailSociete" className="control-label">
              Email
            </label>
            <input
              required
              value={userData.email}
              onChange={updateUserDataHandler("email")}
              type="email"
              className="form-control"
              id="InputEmailSociete"
              aria-describedby="Email"
              placeholder="Entrez votre email"
            />
          </div>
          <div className="form-group">
            <label htmlFor="InputLogo">Logo de votre socit </label>
            <br></br>
            <input
              value={userData.photo}
              onChange={updateUserDataHandler("photo")}
              type="file"
              className="form-control-file"
              id="InputLogo"
            />
          </div>
          <div className="form-group required">
            <label htmlFor="InputInterlocuteur" className="control-label">
              Interlocuteur
            </label>
            <input
              required
              value={userData.interlocuteur}
              onChange={updateUserDataHandler("interlocuteur")}
              type="text"
              className="form-control"
              id="InputInterlocuteur"
              aria-describedby="Interlocuteur"
              placeholder="Entrez le nom de l'interlocuteur principal"
            />
          </div>

          <div className="form-group required">
            <label htmlFor="InputDescriptionSociete" className="control-label">
              Description de la socit
            </label>
            <textarea
              required
              value={userData.description}
              onChange={updateUserDataHandler("description")}
              className="form-control"
              id="InputDescriptionSociete"
              placeholder="Prsentez votre socit succinctement"
              rows="3"
            ></textarea>
          </div>
    </>
  );
}

export default Formenterpriseaccount;
